.account-search {
	min-width: 300px;
}

form {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.account-search h3 {
	padding-bottom: 1.5rem;
}

.cci, .phone-number {
	padding: 0 1rem;
}
.cci {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	padding-bottom: 1rem;
	-ms-flex-align: center;
	    align-items: center;
}


.phone-number {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

.account-search input[type='text'] {
	padding: .4rem .4rem .4rem .8rem;
	font-size: .9rem;
	background: #8EA0B7;
	border: 0;
	text-transform: uppercase;
	color: #102B4F;
	width: 7rem;
	border-radius: 5px;
	outline: none;
}

input[type='text']:focus {
	background: lightblue;
}
.account-search input[type="submit"]{
	width: 100%;
	height: 100%;
	/*margin: 1rem;*/
	position: relative;
	display: block;
	top: 0;
	left: 0;
	font-size: .9rem;
	background: #8EA0B7;
	border: 0;
	text-transform: uppercase;
	color: #102B4F;
	border-radius: 5px;
}

.search-shiny{
	position: relative;
	display: block;
	width: 90%;
	height: 2rem;
	overflow: hidden;
	margin: 16px;
}

.search-shiny:hover::before {
	position: absolute;
  display: block;
  opacity: 0;
  top: 0;
  left: 0;
  content: "";
  height: 3rem;
  width: 120%;
  background: -webkit-gradient( 
  	linear,
  	left top, left bottom,
  	from(rgba(255,255,255,0)),
  	color-stop(1%, rgba(255,255,255,0.03)),
  	color-stop(30%, rgba(255,255,255,0.6)),
  	color-stop(50%, rgba(255,255,255,0.85)),
  	color-stop(70%, rgba(255,255,255,0.85)),
  	color-stop(71%, rgba(255,255,255,0.85)),
  	to(rgba(255,255,255,0))
  );
  background: -webkit-linear-gradient( 
  	rgba(255,255,255,0) 0%,
  	rgba(255,255,255,0.03) 1%,
  	rgba(255,255,255,0.6) 30%,
  	rgba(255,255,255,0.85) 50%,
  	rgba(255,255,255,0.85) 70%,
  	rgba(255,255,255,0.85) 71%,
  	rgba(255,255,255,0) 100%
  );
  background: -o-linear-gradient( 
  	rgba(255,255,255,0) 0%,
  	rgba(255,255,255,0.03) 1%,
  	rgba(255,255,255,0.6) 30%,
  	rgba(255,255,255,0.85) 50%,
  	rgba(255,255,255,0.85) 70%,
  	rgba(255,255,255,0.85) 71%,
  	rgba(255,255,255,0) 100%
  );
  background: linear-gradient( 
  	rgba(255,255,255,0) 0%,
  	rgba(255,255,255,0.03) 1%,
  	rgba(255,255,255,0.6) 30%,
  	rgba(255,255,255,0.85) 50%,
  	rgba(255,255,255,0.85) 70%,
  	rgba(255,255,255,0.85) 71%,
  	rgba(255,255,255,0) 100%
  );
  -webkit-transform: rotate(-20deg);
      -ms-transform: rotate(-20deg);
          transform: rotate(-20deg);
	-webkit-animation: search-shine 2s;
	        animation: search-shine 2s;
  -webkit-animation-timing-function: cubic-beizer(0.17, 0.79, 0.96, 0.25);
          animation-timing-function: cubic-beizer(0.17, 0.79, 0.96, 0.25);
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  z-index: 100;
}


@-webkit-keyframes search-shine {
	0%  { left: -80px; top: -30px; opacity: 0; }
	5% {opacity: 0.3}
	48% {opacity: 0.6}
	80% {opacity: 0.0}
	100% { left: 110px; top: 50px;}
}


@keyframes search-shine {
	0%  { left: -80px; top: -30px; opacity: 0; }
	5% {opacity: 0.3}
	48% {opacity: 0.6}
	80% {opacity: 0.0}
	100% { left: 110px; top: 50px;}
}


