
.header {
	width: 100%;
	min-width: 320px;
	background-color: #03162E;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	z-index: 1;
}

.title {
	height:2rem;
	width: 100%;
	max-width: 500px;
	margin: 1rem;
	display: -ms-flexbox;
	display: flex;
	border-radius: 8px;
}

.nav-bar {
	display: -ms-inline-flexbox;
	display: inline-flex;
	-ms-flex-pack: center;
	    justify-content: center;
}

.nav-bar div {
	width: 8rem;
	height: 2rem;
	margin: 1rem;
	border-radius: 8px;
}

.animation {
	background: -webkit-gradient(linear, left top, right top, from(#BFBFBF) , to(#052454));
	background: -webkit-linear-gradient(left, #BFBFBF , #052454);
	background: -o-linear-gradient(left, #BFBFBF , #052454);
	background: linear-gradient(to right, #BFBFBF , #052454);
	background-size: 190% 100%;
	background-position: left;
	-webkit-animation: pulse 3s infinite ease-in-out;
	        animation: pulse 3s infinite ease-in-out;
}

@-webkit-keyframes pulse {
	0%{
		background-position: right;
	}
	50% {
		background-position: left;
	}
	100% {
		background-position: right;
	}
}

@keyframes pulse {
	0%{
		background-position: right;
	}
	50% {
		background-position: left;
	}
	100% {
		background-position: right;
	}
}

@media (max-width: 900px) {

	.header {
		-ms-flex-direction: column;
		    flex-direction: column;
		margin-top: 1rem;
	}

	.title{
		margin: 0 auto;
	}

	.nav-bar div {
		width: 9.1rem;
	}

}


