.order-summary {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 70%;
	overflow: hidden;
	min-width: 300px;
	margin: 0 2rem 0 4rem;
}

.order-summary p {
	color: #a27f49;
}

.current-services, 
.service-changes {
padding:0 1rem 0 1rem;
}

.changes-display,
.current-services {
	min-height: 120px;
}
.order-summary input {
	position: absolute;
	display: block;
	padding: .4rem;
	font-size: .9rem;
	background: #8EA0B7;
	border: 0;
	text-transform: uppercase;
	color: #102B4F;
	width: 7rem;
	border-radius: 5px;
	text-align: center;
	top: 0;
	left: 0;
	margin: 0;
}

.order-summary-shiny{
	display: block;
	position: relative;
	width: 6.2rem;
	height: 1.1rem;
	overflow: hidden;
	top: 10px;
	margin: 16px;
	padding: .4rem;
}

.order-summary-shiny:hover::before {
	position: absolute;
  display: block;
  top: 0;
  left: 0;
  content: "";
  height: 1rem;
  width: 150px;
  background: -webkit-gradient(  
  	linear,
  	left top, left bottom,
  	from(rgba(255,255,255,0)),
  	color-stop(1%, rgba(255,255,255,0.03)),
  	color-stop(30%, rgba(255,255,255,0.6)),
  	color-stop(50%, rgba(255,255,255,0.85)),
  	color-stop(70%, rgba(255,255,255,0.85)),
  	color-stop(71%, rgba(255,255,255,0.85)),
  	to(rgba(255,255,255,0))
  );
  background: -webkit-linear-gradient(  
  	rgba(255,255,255,0) 0%,
  	rgba(255,255,255,0.03) 1%,
  	rgba(255,255,255,0.6) 30%,
  	rgba(255,255,255,0.85) 50%,
  	rgba(255,255,255,0.85) 70%,
  	rgba(255,255,255,0.85) 71%,
  	rgba(255,255,255,0) 100%
  );
  background: -o-linear-gradient(  
  	rgba(255,255,255,0) 0%,
  	rgba(255,255,255,0.03) 1%,
  	rgba(255,255,255,0.6) 30%,
  	rgba(255,255,255,0.85) 50%,
  	rgba(255,255,255,0.85) 70%,
  	rgba(255,255,255,0.85) 71%,
  	rgba(255,255,255,0) 100%
  );
  background: linear-gradient(  
  	rgba(255,255,255,0) 0%,
  	rgba(255,255,255,0.03) 1%,
  	rgba(255,255,255,0.6) 30%,
  	rgba(255,255,255,0.85) 50%,
  	rgba(255,255,255,0.85) 70%,
  	rgba(255,255,255,0.85) 71%,
  	rgba(255,255,255,0) 100%
  );
  -webkit-transform: rotate(-20deg);
      -ms-transform: rotate(-20deg);
          transform: rotate(-20deg);
	-webkit-animation-name: shine;
	        animation-name: shine;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: cubic-beizer(0.17, 0.79, 0.96, 0.25);
          animation-timing-function: cubic-beizer(0.17, 0.79, 0.96, 0.25);
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  z-index: 100;
}


@-webkit-keyframes shine {
	0%  { left: -40px; top: -20px; opacity: 0; }
	5% {opacity: 0.0}
	48% {opacity: 0.6}
	80% {opacity: 0.0}
	100% { left: 60px; top: 50px;}
}


@keyframes shine {
	0%  { left: -40px; top: -20px; opacity: 0; }
	5% {opacity: 0.0}
	48% {opacity: 0.6}
	80% {opacity: 0.0}
	100% { left: 60px; top: 50px;}
}




